import { Injectable } from '@angular/core';
import { Album } from './Album';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AlbumsService {
  private albums = [
    new Album(0,'On the road','The Beatles',4),
    new Album(1,'Greatest hits','Queen',5),
    new Album(2,'The wall','Pink floyd',4)
  ]
  private url = 'albums';
  constructor(private httpclient: HttpClient) { }

  getAlbums():Observable<Album[]>{
  return this.httpclient.get<Album[]>(this.url + '/all').pipe(
  map (result => result.map( album => new Album(album.id, album.auteur, album.nom, album.note)))
  );
}
  getAlbum(id) : Observable<Album>{
    return this.httpclient.get<Album>(this.url + '/' +id); //GET localhost:4200/api/albums/id
  }

  saveAlbum(album: Album): Observable<any> {
    console.log('save', album);
    return this.httpclient.post(this.url, album); //POST localhost:4200/api/albums
  }

}
