import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlbumsService } from '../../shared/albums.service';

@Component({
  selector: 'app-album-details',
  templateUrl: './album-details.component.html',
  styleUrls: ['./album-details.component.css']
})
export class AlbumDetailsComponent implements OnInit {
  @Input() albumFils;
  @Output() save = new EventEmitter<number>()
  
  constructor(private activatedRoute: ActivatedRoute, private albumsService: AlbumsService, private router:Router) { }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe(
      (map) =>{
      const id = +map.get('id')
      this.albumsService.getAlbum(id).subscribe(
        album => this.albumFils = album
      )
    }
    )

  
  }
  onClick(){

    this.albumsService.saveAlbum(this.albumFils).subscribe(
      () => {
        this.router.navigate(['/album']),
        console.log('ok');
      },
      (err) => console.error(err)
    )
    console.log('save done', this.albumFils.note);
    this.save.emit(this.albumFils.note);
  }

  getColor(){
    if(this.albumFils.note < 0){
      return 'red'
    }else{
      return 'black'
    }
  }

}
