import { Component, OnInit } from '@angular/core';
import { Album } from '../../shared/Album';
import { LoggerService } from '../../../logger.service';
import { AlbumsService } from '../../shared/albums.service';

@Component({
  selector: 'app-album-liste',
  templateUrl: './album-liste.component.html',
  styleUrls: ['./album-liste.component.css']
})
export class AlbumListeComponent implements OnInit {

  albums:Album[];
  selectedAlbum : Album;

  constructor(private loggerService : LoggerService, private albumService: AlbumsService) { }

  ngOnInit() {
    this.albumService.getAlbums().subscribe(
      (albums) => this.albums = albums.map( album => new Album(album.id, album.auteur, album.nom, album.note)),
      (err) => this.loggerService.logError(err)
    )
  }
  selectAlbum(album:Album){
    this.selectedAlbum=album;
    this.loggerService.logDebug(`albmum selected ${this.selectedAlbum.nom}`)
  }

  unselect(){
    delete this.selectedAlbum;
  }

}
